.tab_btns_height {
    height: 35px;

    @include md {
        height: auto;
    }
}

.tab_btns {
    width: 100%;
    background: #fff;
    position: fixed;
    z-index: 10;

    @include md {
        position: static;
    }

    @include lg {
        padding: 0 267px;
    }

    .btn {
        border: 1px solid #DCDCDC;
        width: 33.33333%;
        height: 35px;
        position: relative;

        font-size: 14px;
        line-height: 19px;
        text-transform: uppercase;
        text-align: center;

        justify-content: center;
        align-items: center;
        cursor: pointer;

        @include md {
            height: 69px;
            font-size: 28px;
            line-height: 37px;
        }

        @include lg {
            height: 103px;
            font-size: 42px;
            line-height: 55px;
        }

        //hover下底線
        &::after {
            content: ' ';
            width: 0px;
            height: 4px;
            background-color: $theme_primary;

            position: absolute;
            bottom: 0;
            left: 0;
            transition: 0.3s;
            opacity: 0;

            @include md {
                height: 8px;
            }

            @include lg {
                height: 16px;
            }
        }

        &:hover::after,
        &.active::after {
            width: 100%;
            opacity: 1;
        }

        img {
            width: 15px;
            height: 15px;
            margin-right: 4px;

            @include md {
                width: 32px;
                height: 32px;
                margin-right: 8px;
            }

            @include md {
                width: 48px;
                height: 48px;
            }
        }
    }
}


.content {
    display: none;

    &.active {
        display: block;
    }

    .demo {
        margin-bottom: 30px;
        @include md {
            margin-bottom: 40px;
        }
        @include lg {
            margin-bottom: 88px;
        }

        li {
            width: 50%;
            height: 144px;
            background-size: cover;
            background-position: 100% 70%;
            @include md{
                height: 309px;
            }
        }

        .demo_img1 {
            background-image: url('https://shoppingqhex.github.io/w4/assets/images/optical_bnr01.jpg');
            @include lg{
                margin-right: 30px;
            }
            @include lg{
                background-position: center 60%;
            }
        }

        .demo_img2 {
            background-image: url('https://shoppingqhex.github.io/w4/assets/images/optical_bnr02.jpg');
            @include lg{
                background-position: center 30%;
            }
        }
        .demo_img3 {
            background-image: url('https://shoppingqhex.github.io/w4/assets/images/sun_bnr01.jpg');
            @include lg{
                margin-right: 30px;
            }
            @include lg{
                background-position: center 60%;
            }
        }

        .demo_img4 {
            background-image: url('https://shoppingqhex.github.io/w4/assets/images/sun_bnr02.jpg');
            background-position: center 50%;
            @include lg{
                background-position: center 25%;
            }
        }
    }

    .series_title {
        margin-bottom: 20px;
        @include md {
            margin-bottom: 32px;
        }
        @include lg {
            margin-bottom: 40px;
        }

        .series_title_en {
            font-size: 20px;
            line-height: 26px;
            @include md {
                font-size: 32px;
                line-height: 43px;
            }
            @include lg {
                font-size: 42px;
                line-height: 63px;
            }
        }

        .series_title_zh {
            font-size: 16px;
            line-height: 22px;
            @include md {
                font-size: 24px;
                line-height: 33px;
            }
            @include lg {
                font-size: 32px;
                line-height: 48px;
            }
        }
    }
    .series_cards{
        .series_card {
            width: 100%;
            @include md {
                width: calc(50% - 15px);
            }
            @include lg {
                width: calc(25% - 22.5px);
            }
            .series_card_head {
                width: 100%;
                height: 166px;
                @include md{
                    height: 164px;
                }
            }
    
            .series_card_body {
                justify-content: space-between;
                font-size:16px;
                line-height: 21px;
                text-transform: uppercase;
            }
    
            .series_card_footer {
                .series_box {
                    border-radius: 4px;
                    height: 16px;
                }
    
                .series_box1 {
                    width: 15px;
                    background-color: #5F3E2D;
                    margin-right: 4px;
                }
    
                .series_box2 {
                    width: 17px;
                    background-color: #B75929;
                }
            }
        }
        .series_card_sunglasses{
            @include lg {
                width: calc(33% - 20px);
            }
            .series_card_head {
                @include lg{
                    height: 224px;
                }
            }
        }
        .displaySp{
            display: none;
            @include md {
                display: block;
            }
            @include lg {
                display: none;
            }
        }
    }
    
    .series_pagination{
        justify-content: center;
        margin-top: 40px;
        margin-bottom: 63px;
        @include md {
            margin-top: 105px;
            margin-bottom: 104px;
        }
        @include lg {
            margin-top: 100px;
            margin-bottom: 156px;
        }
        li{
            border: 1px solid #555555;
        }
        a{
            font-weight: normal;
            color: #555555;
        }
        .num{
            padding: 8px 10px 9px 11px;
            &.active,&:hover{
                color: #fff;
                background-color: #000;
            }
        }
        
        .page{
            padding: 8px 8px 7px 9px;
            &.active,&:hover{
                background-color:#F2F2F2;
            }
        }
    }

}