//背景色
.bg-light{
    background-color: $bg_light;
}
.bg-primary{
    background-color: $theme_primary;
}

//字型樣式
.text-white{
    color: #ffffff;
}
.text-gray{
    color: #707070;
}
.text-primary{
    color: $theme_primary;
}
.text-primary-dark{
    color: $theme_primary_dark;
}


.text-center{
    text-align: center;
}
.text-bold{
    font-weight:bold;
}
.text-medium{
    font-weight: 500;
}
.text-italic{
    font-style: italic;
}


//間距
$spacing: (
    "mt": 'margin-top',
    "mb": 'margin-bottom',
    "ml": 'margin-left',
    "mr": 'margin-right',
    "pt": 'padding-top',
    "pb": 'padding-bottom',
    "pl": 'padding-left',
    "pr": 'padding-right',
);

@each $key,$value in $spacing {
    @for $i from 1 through 20{
        .#{$key}-#{$i} {
            #{$value} : $i * 0.5rem;
        }
        .mx-#{$i} {
            margin-left: $i * 0.5rem;
            margin-right: $i * 0.5rem;
        }
        .my-#{$i} {
            margin-top: $i * 0.5rem;
            margin-bottom: $i * 0.5rem;
        }
        .px-#{$i} {
            padding-left: $i * 0.5rem;
            padding-right: $i * 0.5rem;
        }
        .py-#{$i} {
            padding-top: $i * 0.5rem;
            padding-bottom: $i * 0.5rem;
        }  
    }
    .#{$key}-0{
        #{$value} : 0 !important;  
    }
    .#{$key}-md-0{
        @include md{
            #{$value} : 0 !important;
        }  
    }
    .#{$key}-lg-0{
        @include lg{
            #{$value} : 0 !important;
        }  
    }
}

.mt-4p{
    margin-top: 4px;
}

@include md{
    .mb-md-0{
        margin-bottom: 0;
    }
    .mr-md-30p{
        margin-right: 30px;
    }
    .mb-md-72p{
        margin-bottom: 72px;
    }
}
@include lg{
    .mb-lg-0{
        margin-bottom: 0 !important;
    }
    .mt-lg-4{
        margin-top: 32px;
    }
    .mr-lg-30p{
        margin-right: 30px;
    }
    .mb-lg-104p{
        margin-bottom: 104px;
    }

}

//display
$display:  'none','static' , 'block' , 'flex' ;

@each $value in $display {
    .d-#{$value}{
        display:#{$value} !important;
    }
    @include md{
        .d-md-#{$value}{
            display:#{$value} !important;
        }
    }
    @include lg{
        .d-lg-#{$value}{
            display:#{$value} !important;
        }
    }
}