header{
    height: 138px;
    @include md {
        height: auto;
    }
}
nav {
    width: 100%;
    height: 138px;
    background-color: $theme_primary;

    display: flex;
    flex-direction: column;
    align-items: center;
    
    position: fixed;
    z-index: 11;

    @include md {
        height: 108px;
        padding: 0 39px;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        
        position: static;
    }
    @include lg {
        padding: 0 88px;
    }


    h1 {
        height: 50px;
        display: flex;
        align-items: center;

        a {
            background-image: url('https://hexschool.github.io/webLayoutTraining1st/week3/logo.png');
            background-size: cover;
            background-position: center center;
            width: 48px;
            height: 29.14px;
            display: block;
            text-indent: 101%;
            overflow: hidden;
            white-space: nowrap;
            
            @include md {
                width: 80px;
                height: 48.57px;
            }
        }
    }


    .links {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;


        font-size: 20px;
        line-height: 28px;
        @include md {
            width: 420px;
        }
        @include lg {
            width: 492px;
        }
        
        .link{
            border: 1px solid #fff;
            width: 50%;
            height: 44px;
            line-height: 44px;
            @include md {
                border: 0;
                width: auto;
            }
            a{
                display: block;
                &:hover {
                    color: $theme_primary;
                    background-color: #fff;
                }
            }
        }
    }
}

